
.container {
    text-align: right;
    }
    .myTheme{
        display: inline-block;
        position: relative;
        right: 18px;
        color: white;
        font-weight: 550;
        font-size: 0.9em;
    }

    .myTheme2{
        display: inline-block;
        position: relative;
        right: 18px;
        color: hsl(60, 10%, 19%);
        font-weight: 550;
        font-size: 0.9em;
    }

    .toggle-switch {
    position: relative;
    width: 75px;
    display: inline-block;
    text-align: left;
    top: 8px;
    }
    .checkbox {
    display: none;
    }
    .label {
    display: block;
    overflow: hidden;
    cursor: pointer;
    border: 0 solid #bbb;
    border-radius: 30px;
    }
    .inner {
    display: block;
    width: 200%;
    margin-left: -100%;
    transition: margin 0.3s ease-in 0s;
    }
    .inner2 {
        display: block;
        width: 200%;
        margin-left: -100%;
        transition: margin 0.3s ease-in 0s;
        }

    .inner:before,
    .inner:after {
    float: left;
    width: 50%;
    height: 30px;
    padding: 0;
    line-height: 30px;
    color: #fff;
    font-weight: bold;
    box-sizing: border-box;
    }

    .inner2:before,
    .inner2:after {
    float: left;
    width: 50%;
    height: 30px;
    padding: 0;
    line-height: 30px;
    color: #fff;
    font-weight: bold;
    box-sizing: border-box;
    }

    .inner:before {
    content: "";
    padding-left: 10px;
    background-color: hsl(0, 5%, 81%);
    color: #fff;
    }

    .inner2:before {
        content: "";
        padding-left: 10px;
        background-color: hsl(0, 5%, 81%);
        color: #fff;
        }

    .inner:after {
    content: "";
    padding-right: 10px;
    background-color: hsl(223, 31%, 20%);
    color: #fff;
    text-align: right;
    }

    .inner2:after {
        content: "";
        padding-right: 10px;
        background-color: hsl(0, 5%, 81%);
        color: #fff;
        text-align: right;
        }

    .switch {
    display: block;
    width: 24px;
    margin: 5px;
    background: #d03f2f;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 40px;
    border: 0 solid #bbb;
    border-radius: 40px;
    transition: all 0.3s ease-in 0s;
    }
    .checkbox:checked + .label .inner {
    margin-left: 0;
    }
    .checkbox:checked + .label .switch {
    right: 0px;
    }

    .switch2 {
        display: block;
        width: 24px;
        margin: 5px;
        background: hsl(25, 98%, 40%);
        position: absolute;
        top: 0;
        bottom: 0;
        right: 40px;
        border: 0 solid #bbb;
        border-radius: 40px;
        transition: all 0.3s ease-in 0s;
        }
        .checkbox:checked + .label .inner2 {
        margin-left: 0;
        }
        .checkbox:checked + .label .switch2 {
        right: 0px;
        }
    